const validate = (nameSelector,mailSelector,phoneSelector) => {

    const name = document.querySelector(nameSelector),
          mail = document.querySelector(mailSelector),
          phone = document.querySelector(phoneSelector),
          phoneReg = [
            /([\d]{2}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{2})$/,
            /^([\d]{2}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{3})$/,
            /^([\d]{2}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{3})$/,
            /^([\d]{3}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{2})$/,
            /^([\d]{2}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{2})$/,
            /^([\d]{3}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{3}[\s|-]{1}[\d]{2})$/,
            /^([\d]{3}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{2}[\s|-]{1}[\d]{3})$/,
            /^([\d]{1,10})$/
          ],
          nameReg = /^[a-zа-яё ]{3,15}$/i,
          emailReg = /^[a-z0-9_-]{3,15}\@[a-z]{4,}\.[a-z]{2,}$/i;

    let formError = true;

    function checkValidName(name) {
        let nameError = document.createElement('div');

        name.addEventListener('input', function() {
            let validName = name.value.match(nameReg);
            nameError.remove();
            name.style.border = '';
            name.style.outline = '#000000';

            if (validName) {
                formError = false;

            } else {
                formError = true;

                nameError.textContent = "Ваше имя может содержать пробелы и быть от 3 до 15 символов";
                nameError.classList.add('formError');
                name.after(nameError);
                name.style.outline = 'none';
                name.style.border = '2px solid red';
            }

        });
    }

    function checkValidMail(email) {
        let mailError = document.createElement('div');
        email.addEventListener('input', function () {
            let validMail = mail.value.match(emailReg);
            mailError.remove();
            mail.style.border = '';
            mail.style.outline = '#000000';

            if (validMail) {
                formError = false;

            } else {
                formError = true;

                mailError.textContent = "Ваш E-mail должен соответствовать username@example.com";
                mailError.classList.add('formError');
                mail.after(mailError);
                mail.style.outline = 'none';
                mail.style.border = '2px solid red';

            }
        });
    }

    function checkValidPhone(phone) {
        let phoneError = document.createElement('div');
        phone.addEventListener('input', function () {
            let validPhone = false;
            phoneReg.forEach( reg => {
                if (!!phone.value.match(reg)) {
                    validPhone = true;
                    console.log(true);
                }
            });
            phoneError.remove();
            phone.style.border = '';

            if (validPhone) {
                formError = false;

            } else {
                formError = true;

                phoneError.textContent = "Максимум 10 цифр без тире и пробелов или с ними";
                phoneError.classList.add('formError');
                phone.after(phoneError);

                phone.style.outline = 'none';
                phone.style.border = '2px solid red';
            }
        });
    }

    checkValidName(name);
    checkValidMail(mail);
    checkValidPhone(phone);
};

export default validate;