const explore = (windowSelector, imgSelector, lineSelector) => {

    const img = document.querySelector(imgSelector),
          windowImg = document.querySelector(windowSelector),
          line = document.querySelector(lineSelector);

    let clicked = 0,
        w = windowImg.offsetWidth,
        h = windowImg.offsetHeight;

    function compareImages() {
        img.style.width = (w / 2) + "px";
        line.style.left = (w / 2) + "px";

        windowImg.addEventListener("mousedown", slideReady);
        window.addEventListener("mouseup", slideFinish);
        windowImg.addEventListener("touchstart", slideReady);
        window.addEventListener("touchstop", slideFinish);
    }

    function slideReady(e) {
        e.preventDefault();
        clicked = 1;
        window.addEventListener("mousemove", slideMove);
        window.addEventListener("touchmove", slideMove);
    }

    function slideMove(e) {
        let pos;
        if (clicked == 0) {
            return false;
        }    
        pos = getCursorPos(e);
        if (pos < 0) {
            pos = 0;
        }    
        if (pos > w) {
            pos = w;
        }    
        slide(pos);
    }

    function getCursorPos(e) {
        let a, x = 0;
        e = e || window.event;

        a = windowImg.getBoundingClientRect();
        x = e.pageX - a.left;

        x = x - window.pageXOffset;
        return x;
    }

    function slide(x) {
        img.style.width = x + "px";
        line.style.left = x + "px";
    }

    function slideFinish() {
        clicked = 0;
    }

    compareImages();
};

export default explore;