const ticketsCalc = (radioSelector, basikSelector, seniorSelector, totalSelector,wrapperSelector,btnSelector) => {
    const radio = document.querySelectorAll(radioSelector),
          basicCount = document.querySelector(basikSelector),
          seniorCount = document.querySelector(seniorSelector),
          total = document.querySelector(totalSelector),
          price = {
            'one': 20,
            'two': 25,
            'three': 40 
          };
   
    let totalCount = 0;
    let type = null;

    const myStorage = window.localStorage;

    radio.forEach( btn => {
        btn.addEventListener('change', () => {
            myStorage.setItem('name', btn.parentNode.textContent);
            type = price[btn.value];
            myStorage.setItem('type', btn.value);
            calc(type, basicCount.value, seniorCount.value);
        });
    });

    if (myStorage.getItem('type')) {
        if (myStorage.getItem('type') === 'one') {
            type = price['one'];
            radio[0].checked = true;
        } else if (myStorage.getItem('type') === 'two') {
            type = price['two'];
            radio[1].checked = true;
        } else if (myStorage.getItem('type') === 'three') {
            type = price['three'];
            radio[2].checked = true;
        }
    }

    if (myStorage.getItem('basik')) {
        basicCount.value = myStorage.getItem('basik');
    }

    if (myStorage.getItem('senior')) {
        seniorCount.value = myStorage.getItem('senior');
    }

    calc(type,basicCount.value,seniorCount.value);

    function calc(type, basic = 0, senior = 0) {
        let x = +basic * +type;
        let y = +senior * Math.ceil(+type / 2);
        totalCount = x + y;
        total.textContent = totalCount;
    }


    const wrapper = document.querySelectorAll(wrapperSelector);

    wrapper.forEach( (item,j) => {
        const btns = item.querySelectorAll(btnSelector),
              left = btns[0],
              right = btns[1],
              input = item.querySelector('input');
        let i = input.value;

        right.addEventListener('click', e => {
            e.preventDefault();

            ++i;
            if (i >= 21) {
                i = 0;
            }
            input.value = i;
            if (j === 0) {
                myStorage.setItem('basik', i);
            } else if (j === 1) {
                myStorage.setItem('senior', i);
            }
            
            calc(type, basicCount.value, seniorCount.value);
        });
        left.addEventListener('click', e => {
            e.preventDefault();

            --i;
            if (i < 0) {
                i = 20;
            }
            input.value = i;
            if (j === 0) {
                myStorage.setItem('basik', i);
            } else if (j === 1) {
                myStorage.setItem('senior', i);
            }
            calc(type, basicCount.value, seniorCount.value);
        });
    });

};

export default ticketsCalc;