const mainForm = (typeSelector,countSelectors,totalSelector,wrapperSelector,btnSelector,optionSelector) => {
    const ticketType = document.querySelector(typeSelector),
          counts = document.querySelectorAll(countSelectors),
          basik = counts[0],
          senior = counts[1],
          total = document.querySelector(totalSelector),
          basicCount = document.querySelector('#basikInput'),
          seniorCount = document.querySelector('#seniorInput'),
          options = document.querySelectorAll(optionSelector),
          myStorage = window.localStorage,
          price = {
            'one': 20,
            'two': 25,
            'three': 40 
          };

    let totalCount = 0;
    let type = null;

    function calc(type, basic = 0, senior = 0) {
        let x = +basic * +type;
        let y = +senior * Math.ceil(+type / 2);
        totalCount = x + y;
        total.textContent = totalCount;
    }

    function setCost(input) {
        if (input === 'basik') {
            basik.textContent = +type * +basicCount.value;
        } else if (input === 'senior') {
            senior.textContent = Math.ceil((+type / 2) * +seniorCount.value);
        }
    }

    document.querySelector('.overlay__select-menu').addEventListener('click', () => {
        options.forEach( option => {
            if (option.hasAttribute('selected')) {
                myStorage.setItem('name', option.textContent);
                type = price[option.value];
                myStorage.setItem('type', option.value);
                document.querySelector('.overlay__overview-temp div').textContent = option.textContent;
            }
        });
        calc(type, basicCount.value, seniorCount.value);
        setCost('basik');
        setCost('senior');
    });

    const wrapper = document.querySelectorAll(wrapperSelector),
          counter = document.querySelectorAll('.overlay__overview-num');


    wrapper.forEach( (item,j) => {
        const btns = item.querySelectorAll(btnSelector),
            left = btns[0],
            right = btns[1],
            input = item.querySelector('input');
        let i = input.value;

        right.addEventListener('click', e => {
            e.preventDefault();

            ++i;
            if (i >= 21) {
                i = 0;
            }
            input.value = i;
            if (j === 0) {
                myStorage.setItem('basik', i);
                counter[0].textContent = basicCount.value;
            } else if (j === 1) {
                counter[1].textContent = seniorCount.value;
                myStorage.setItem('senior', i);
            }
            calc(type, basicCount.value, seniorCount.value);
            setCost('basik');
            setCost('senior');
        });
        left.addEventListener('click', e => {
            e.preventDefault();

            --i;
            if (i < 0) {
                i = 20;
            }
            input.value = i;
            if (j === 0) {
                myStorage.setItem('basik', i);
                counter[0].textContent = basicCount.value;
            } else if (j === 1) {
                counter[1].textContent = seniorCount.value;
                myStorage.setItem('senior', i);
            }
            calc(type, basicCount.value, seniorCount.value);
            setCost('basik');
            setCost('senior');
        });
    });

    document.querySelector('.tickets__amount-submit').addEventListener('click', e => {
        e.preventDefault();

        if (myStorage.getItem('basik')) {
            basicCount.value = myStorage.getItem('basik');
        }
    
        if (myStorage.getItem('senior')) {
            seniorCount.value = myStorage.getItem('senior');
        }
    
        if (myStorage.getItem('type')) {
            options.forEach( option => {
                option.removeAttribute('selected');
            });
            if (myStorage.getItem('type') === 'one') {
                type = price['one'];
                options[0].setAttribute('selected','selected');
            } else if (myStorage.getItem('type') === 'two') {
                type = price['two'];
                options[1].setAttribute('selected','selected');
            } else if (myStorage.getItem('type') === 'three') {
                type = price['three'];
                options[2].setAttribute('selected','selected');
            }
            try {
                document.querySelector('.overlay__select span').remove();
            } catch(e) {}
        }

        document.querySelector('.overlay__overview-temp div').textContent = myStorage.getItem('name');

        calc(type, basicCount.value, seniorCount.value);
        setCost('basik');
        setCost('senior');
    });

    const date = new Date();
    document.querySelector('#date').setAttribute('min', `${date.getFullYear()}-${+date.getMonth() + 1}-${date.getDate()}`);


    document.querySelector('#date').addEventListener('input', e => {
        const date = new Date(document.querySelector('#date').value);
        let total = '';
    
        if (date.getDay() == '0') {
            total += 'Sunday' + ', ';
        } else if (date.getDay() == '1') {
            total += 'Monday' + ', ';   
        } else if (date.getDay() == '2') {
            total += 'Tuesday' + ', ';
        } else if (date.getDay() == '3') {
            total += 'Wednesday' + ', ';
        } else if (date.getDay() == '4') {
            total += 'Thursday' + ', ';
        } else if (date.getDay() == '5') {
            total += 'Friday' + ', ';
        } else if (date.getDay() == '6') {
            total += 'Saturday' + ', ';
        }
        
        if (date.getMonth() == 0) {
            total += 'January ';
        } else if (date.getMonth() == 1) {
            total += 'February ';
        } else if (date.getMonth() == 2) {
            total += 'March ';
        } else if (date.getMonth() == 3) {
            total += 'April ';
        } else if (date.getMonth() == 4) {
            total += 'May ';
        } else if (date.getMonth() == 5) {
            total += 'June ';
        } else if (date.getMonth() == 6) {
            total += 'July ';
        } else if (date.getMonth() == 7) {
            total += 'August ';
        } else if (date.getMonth() == 8) {
            total += 'September ';
        } else if (date.getMonth() == 9) {
            total += 'October ';
        } else if (date.getMonth() == 10) {
            total += 'November ';
        } else if (date.getMonth() == 11) {
            total += 'December ';
        }

        total += date.getDate();

        document.querySelector('.overlay__overview-whenDate div').textContent = total;
    });

    document.querySelector('#times').addEventListener('input', e => {
        const arr = document.querySelector('#times').value.split(':');
        let total = '';
        if (arr[0] < 9) {
            total += '09';
        } else if (arr[0] > 18) {
            total += '18';
        } else {
            total += arr[0];
        }

        total += ':';

        if (arr[1] <= 15 || arr[1] >= 45) {
            total += '00';
        } else if (arr[1] > 15 && arr[1] < 45 && arr[0] != 18) {
            total += '30';
        } else {
            total += '00';
        }

        document.querySelector('.overlay__overview-whenTime div').textContent = total;
        document.querySelector('#times').value = total;
    });
};

export default mainForm;