const videoSlider = (sliderSelector,itemsSelector,mainVideoSelector,leftSelector,rightSelector,dotsSelector,dotsActive) => {
    const slider = document.querySelector(sliderSelector),
          mainVideo = document.querySelector(mainVideoSelector),
          left = document.querySelector(leftSelector),
          right = document.querySelector(rightSelector),
          dots = document.querySelectorAll(dotsSelector),
          progres = document.querySelector('.video__progres');

    let items = document.querySelectorAll(itemsSelector),
        count = 0;
    items.forEach( item => {
        item.classList.add('animate__animated');
    });

    function showSlide(vector) {
        if (vector === 'right') {
            setDotActive(1);
            items[0].classList.add('animate__fadeOutLeft');
            items[1].classList.add('animate__fadeInLeft');
            slider.appendChild(items[0]);
            items = document.querySelectorAll(itemsSelector);
            items[items.length - 1].classList.remove('animate__fadeOutLeft', 'animate__fadeInLeft');
            setMainVideo();
        } else if (vector === 'left') {
            setDotActive(-1);
            items[0].classList.add('animate__fadeOutRight');
            let active = items[items.length - 1];
            slider.insertBefore(active,items[0]);
            items = document.querySelectorAll(itemsSelector);
            items[0].classList.add('animate__fadeInRight');
            items[1].classList.remove('animate__fadeOutRight', 'animate__fadeInRight');
            items[items.length - 1].classList.remove('animate__fadeInRight');
            setMainVideo();
        }
    }

    dots.forEach( (dot,i) => {
        dot.addEventListener('click', () => {
            if (i > count) {
                for (let j = 0; j < i - count; j++) {
                    slider.appendChild(items[0]);
                    items = document.querySelectorAll(itemsSelector);
                }
                setDotActive( i - count);
                setMainVideo();
            } else if (i < count) {
                for (let k = 0; k < count - i; k++) {
                    let active = items[items.length - 1];
                    slider.insertBefore(active,items[0]);
                    items = document.querySelectorAll(itemsSelector);
                }
                setDotActive(i - count);
                setMainVideo();
            }
        });
    });

    function setMainVideo() {
        mainVideo.pause();
        document.querySelector('#playVideo').src = 'img/video/play.svg';
        document.querySelector('.video__play').style.display = 'flex';
        mainVideo.src = `img/video/video${count}.mp4`;
        mainVideo.poster = `img/video/poster${count}.webp`;
    }

    function countPlus(n) {
        count += n;
        if (count < 0) {
            count = items.length - 1;
        }
        if (count > items.length - 1) {
            count = 0;
        }
    }

    function setDotActive(n) {
        countPlus(n);
        dots.forEach( (dot,i) => {
            dot.classList.remove(dotsActive);

            if (i === count) {
                dot.classList.add(dotsActive);
            }
        });
    }

   

    left.addEventListener('click', () => {
        showSlide('left');
    });
    right.addEventListener('click', () => {
        showSlide('right');
    });
};

export default videoSlider;