const slider = (slidesSelector, arrowRight, arrowLeft, dotsSelector, active, countSelector) => {
    let slideIndex = 0,
        cursor = 0;

    const slides = document.querySelectorAll(slidesSelector),
          dots = document.querySelectorAll(dotsSelector),
          count = document.querySelector(countSelector);
    
    function showIndexSlide(index) {
        if (index > slides.length - 1) {
            slideIndex = 0;
        }

        if (index < 0) {
            slideIndex = slides.length - 1;
        }

        slides.forEach( item => {
            item.style.display = 'none';
            item.classList.add('animate__animated');
        });

        slides[slideIndex].style.display = 'block';
        activeDots(slideIndex);
    }

    showIndexSlide();

    function plusSlide(n) {
        showIndexSlide(slideIndex += n);
        count.textContent = '0' + (+slideIndex + 1);
    }

    const right = document.querySelector(arrowRight),
          left = document.querySelector(arrowLeft);

    right.addEventListener('click', () => {
        plusSlide(1);

        slides[slideIndex].classList.remove('animate__fadeInLeft');
        slides[slideIndex].classList.add('animate__fadeInRight');
    });      

    left.addEventListener('click', () => {
        plusSlide(-1);

        slides[slideIndex].classList.remove('animate__fadeInRight');
        slides[slideIndex].classList.add('animate__fadeInLeft');
    });
    
    function activeDots(index) {
        dots.forEach( (dot,i) => {
            dot.classList.remove(active);
            if (i === index) {
                dot.classList.add(active);
            }
        });
    }

    dots.forEach( (dot,i) => {
        dot.addEventListener('click', () => {
            if (i > slideIndex) {
                plusSlide( i - slideIndex);

                slides[slideIndex].classList.remove('animate__fadeInLeft');
                slides[slideIndex].classList.add('animate__fadeInRight');
            } else if (i < slideIndex) {
                plusSlide(i - slideIndex);

                slides[slideIndex].classList.remove('animate__fadeInRight');
                slides[slideIndex].classList.add('animate__fadeInLeft');
            }
        });
    });

    slides[0].closest('.welcome__wrapper').addEventListener('mousedown', e => {
        cursor = e.clientX;
    });

    slides[0].closest('.welcome__wrapper').addEventListener('mouseup', e => {
        if (e.clientX > (cursor + 50)) {
            left.click();
        } else if(e.clientX < (cursor - 50)) {
            right.click();
        }
    });
};

export default slider;