const video = (volumeSelector,progresSelector,videoSelector,playSelector,bigPlaySelector,volumeImgSelector,fullSelector,messageSelector) => {

    const volume = document.querySelector(volumeSelector),
          progres = document.querySelector(progresSelector),
          video = document.querySelector(videoSelector),
          play = document.querySelector(playSelector),
          bigPlay = document.querySelector(bigPlaySelector),
          volumeImg = document.querySelector(volumeImgSelector),
          full = document.querySelector(fullSelector),
          message = document.querySelector(messageSelector),

    imgObj = {
        play: 'img/video/play.svg',
        pause: 'img/video/pause.svg',
        volume: 'img/video/volume.svg',
        mute: 'img/video/mute.svg',
        full: 'img/video/Frame.svg',
        small: 'img/video/smalScreen.svg'
    };

    let preValue = volume.value;
    progres.style.background = `linear-gradient(to right, #710707 ${+progres.value}%, #C4C4C4 ${+progres.value + 1}%)`;

    function launchFullScreen(element) {
        if(element.requestFullScreen) {
          element.requestFullScreen();
        } else if(element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if(element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        }
    }

    function cancelFullscreen() {
        if(document.cancelFullScreen) {
          document.cancelFullScreen();
        } else if(document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if(document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
    }

    let flag = false;
    window.addEventListener('keydown', function(e){
        if(e.key == 'Shift') {
            flag = true;
        }
    }, false);

    window.addEventListener('keyup', function(e){
        if(e.key == 'Shift') {
            flag = false;
        }
    }, false);

    function playToggle() {
        const status = video.paused ? 'play' : 'pause';
        video[status]();
        play.src = play.src.slice(-8) === 'play.svg' ? imgObj.pause: imgObj.play;
        bigPlay.style.display = window.getComputedStyle(bigPlay).display === 'flex' ? 'none' : 'flex';
    }

    play.addEventListener('click', playToggle);

    video.addEventListener('click', playToggle);

    bigPlay.addEventListener('click', playToggle);

    volume.addEventListener('input' , function() {
        const value = this.value;
        this.style.background = `linear-gradient(to right, #710707 ${+value * 100}%, #C4C4C4 ${+value * 100 + 1}%)`;
        video.volume = volume.value;
  
        if (value == 0) {
            volumeImg.src = imgObj.mute;
        } else {
            volumeImg.src = imgObj.volume;
        }
    }); 

    volumeImg.addEventListener('click', function() {
        if (volumeImg.src.slice(-10) === 'volume.svg') {
            volumeImg.src = imgObj.mute;
            preValue = volume.value;
            video.volume = 0;
            volume.value = 0;
            volume.style.background = `linear-gradient(to right, #710707 ${+volume.value * 100}%, #C4C4C4 ${+volume.value * 100 + 1}%)`;
        } else {
            volumeImg.src = imgObj.volume;        
            volume.value = preValue;
            video.volume = volume.value;
            volume.style.background = `linear-gradient(to right, #710707 ${+volume.value * 100}%, #C4C4C4 ${+volume.value * 100 + 1}%)`;
        }
    });

    video.addEventListener('timeupdate', function() {
        progres.setAttribute('step', (video.duration / 100));
        const value = (video.currentTime / video.duration) * 100;
        progres.value = value;
        progres.style.background = `linear-gradient(to right, #710707 ${+progres.value}%, #C4C4C4 ${+progres.value}%)`;
    });

    video.addEventListener('ended' , function() {
        progres.value = 0;
        progres.style.background = `linear-gradient(to right, #710707 ${+progres.value}%, #C4C4C4 ${+progres.value}%)`;
        play.src = imgObj.play;
        bigPlay.style.display = 'flex';
    });

    progres.addEventListener('input', function() {
        video.currentTime = this.value * video.duration / 100;
        const value = (video.currentTime / video.duration) * 100;
        progres.value = value;
        progres.style.background = `linear-gradient(to right, #710707 ${+progres.value}%, #C4C4C4 ${+progres.value}%)`;
    });

    full.addEventListener('click', function() {
        if (full.src.slice(-9) === 'Frame.svg') {
            launchFullScreen(video.closest('.video__main'));
            full.src = imgObj.small;
        } else {
            cancelFullscreen(video);
            full.src = imgObj.full;
        }
    });

    window.addEventListener('keydown', function(e) {
        
        if (window.getComputedStyle(document.querySelector('.overlay')).display === 'none') {

            if (e.code === 'Space') {
                playToggle();
            } else if (e.code === 'KeyM') {
                volumeImg.click();
            } else if (e.code === 'KeyF') {
                full.click();
            } else if (e.code === 'Comma' && flag) {
                if (video.playbackRate > 0.25) {
                    video.playbackRate = +video.playbackRate - 0.25;
                    message.textContent = `speed: ${video.playbackRate}`;
                    message.style.display = 'block';
                } else {
                    message.textContent = `speed: ${video.playbackRate}`;
                    message.style.display = 'block';
                }
                setTimeout( () => {
                    message.style.display = 'none';
                },2000);
            } else if (e.code === 'Period' && flag) {
                if (video.playbackRate < 2) {
                    video.playbackRate = +video.playbackRate + 0.25;
                    message.textContent = `speed: ${video.playbackRate}`;
                    message.style.display = 'block';
                } else {
                    message.textContent = `speed: ${video.playbackRate}`;
                    message.style.display = 'block';
                }
                setTimeout( () => {
                    message.style.display = 'none';
                },2000);
            }

        }
        
    });
};

export default video;